.expanded {
  background-color: var(--orange);
  color: #ffffff;
  transition: left 0.35s linear;
  display: block !important;
  position: static;
  top: 0px;
  bottom: 0px;
  z-index: 777;
  left: 0;
  position: fixed;
  width: 30%;
}
@media (max-width: 992px) {
  .expanded {
    width: 40%;
  }
}
@media (max-width: 768px) {
  .expanded {
    width: 50%;
  }
}
@media (max-width: 576px) {
  .expanded {
    width: 100%;
  }
}
